<template>
  <div class="ui container">
    <div
      class="
        ui
        form
        segment
        padded
        raised
        animate__animated animate__fadeInDownBig
      "
    >
      <div class="field">
        <label>User</label>
        <input type="text" placeholder="User" v-model="form.user" />
      </div>
      <div class="field">
        <label>Password</label>
        <input type="password" placeholder="Password" v-model="form.password" />
      </div>
      <div class="field">
        <button
          class="fluid ui button primary"
          @click="login"
          :class="requestform ? 'loading disabled' : ''"
        >
          Login
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../services/axios";
export default {
  name: "login",
  data: () => {
    return {
      form: {
        user: null,
        password: null,
      },
      requestform: false,
    };
  },
  methods: {
    login() {
      this.requestform = true;
      axios.post("/api/useradmin/login", this.form).then((res) => {
        this.requestform = false;
        if (res.data["status"] == 200) {
          this.$session.set("token", res.data["token"]);
          this.$router.push("/tandas");
        }
      });
    },
  },
};
</script>

<style lang="scss">
body {
  background: #f5f5f5;
}
.ui.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
